import request from '@/utils/request';

/**
 * 
 * @param {string} levelId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: Array<{
 *     id: string,
 *     level_id: string,
 *     stem: string,
 *     content_json: string,
 *     answer_json: string,
 *     score: number,
 *     difficulty: number,
 *     created_by: string,
 *     created_at: string
 *   }>
 * }>}
 */
export function getLevelQuestions(levelId) {
    return request({
        url: `/api/v1/levels/${levelId}/questions`,
        method: 'GET'
    });
}

/**
 * 
 * @param {string} levelId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: {
 *     user_id: string,
 *     level_id: string,
 *     status: number,
 *     started_at: string
 *   }
 * }>}
 */
export function startLevel(levelId) {
    return request({
        url: `/api/v1/levels/${levelId}/start`,
        method: 'POST'
    });
}

/**
 * 
 * @param {string} levelId - ID
 * @param {Object} data - 
 * @param {string} data.question_id - ID
 * @param {Object} data.answer_json - 
 * @param {number} data.duration_ms - 
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: {
 *     question_id: string,
 *     is_correct: boolean,
 *     score: number,
 *     total_score: number
 *   }
 * }>}
 */
export function submitAnswer(levelId, questionId, answerJson, durationMs) {
    return request({
        url: `/api/v1/levels/${levelId}/submit`,
        method: 'POST',
        data: {
            question_id: questionId,
            answer_json: answerJson,
            duration_ms: durationMs
        }
    });
}

/**
 * 
 * @param {string} levelId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: {
 *     user_id: string,
 *     level_id: string,
 *     score: number,
 *     stars: number,
 *     completed_at: string
 *   }
 * }>}
 */
export function completeLevel(levelId) {
    return request({
        url: `/api/v1/levels/${levelId}/complete`,
        method: 'POST'
    });
}

/**
 * 
 * @param {string} levelId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: {
 *     id: string,
 *     paper_id: string,
 *     paper_author: string,
 *     paper_pub_ym: string,
 *     citation_count: number,
 *     name: string,
 *     pass_condition: string,
 *     meta_json: string,
 *     created_at: string,
 *     updated_at: string
 *   }
 * }>}
 */
export function getLevel(levelId) {
    return request({
        url: `/api/v1/levels/${levelId}`,
        method: 'GET'
    });
} 