import request from '@/utils/request'

/**
 * 
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: Array<{
 *     id: string,
 *     name: string,
 *     description: string,
 *     created_at: string,
 *     updated_at: string
 *   }>
 * }>}
 */
export function getAllSubjects() {
  return request({
    url: '/api/v1/subjects',
    method: 'GET'
  })
}

/**
 * 
 * @param {string} subjectId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: Array<{
 *     id: string,
 *     subject_id: string,
 *     title: string,
 *     citation: string,
 *     created_at: string,
 *     updated_at: string
 *   }>
 * }>}
 */
export function getSubjectPapers(subjectId) {
  return request({
    url: `/api/v1/subjects/${subjectId}/papers`,
    method: 'GET'
  })
} 