import request from '@/utils/request'

/**
 * 
 * @param {Object} data - 
 * @param {string} data.email - 
 * @param {string} data.password - 
 * @returns {Promise<{
 *   user: {
 *     id: string,
 *     email: string,
 *     display_name: string,
 *     avatar_url: string,
 *     eth_address: string,
 *     created_at: string,
 *     updated_at: string
 *   },
 *   access_token: string,
 *   refresh_token: string,
 *   token_type: string,
 *   expires_in: number
 * }>}
 */
export function login(data) {
  return request({
    url: '/api/v1/auth/login',
    method: 'POST',
    data
  })
}

/**
 * 
 * @param {Object} data - 
 * @param {string} data.email - 
 * @param {string} data.password - 
 * @param {string} data.display_name - 
 * @returns {Promise<{
 *   user: {
 *     id: string,
 *     email: string,
 *     display_name: string,
 *     avatar_url: string,
 *     eth_address: string,
 *     created_at: string,
 *     updated_at: string
 *   },
 *   access_token: string,
 *   refresh_token: string,
 *   token_type: string,
 *   expires_in: number
 * }>}
 */
export function register(data) {
  return request({
    url: '/api/v1/auth/register',
    method: 'POST',
    data
  })
}

/**
 * 
 * @returns {Promise<{
 *   access_token: string,
 *   refresh_token: string,
 *   token_type: string,
 *   expires_in: number
 * }>}
 */
export function refreshToken() {
  return request({
    url: '/api/v1/auth/refresh',
    method: 'POST'
  })
}

/**
 * 
 * @returns {Promise<void>}
 */
export function logout() {
  return request({
    url: '/api/v1/users/logout',
    method: 'POST'
  })
} 