import request from '@/utils/request'

/**
 * 
 * @param {string} questionId - ID
 * @returns {Promise<{
 *   success: boolean,
 *   message: string,
 *   data: {
 *     id: string,
 *     level_id: string,
 *     stem: string,
 *     content_json: string,
 *     answer_json: string,
 *     score: number,
 *     difficulty: number,
 *     created_by: string,
 *     created_at: string
 *   }
 * }>}
 */
export function getQuestion(questionId) {
  return request({
    url: `/api/v1/questions/${questionId}`,
    method: 'GET'
  })
}

