import request from '@/utils/request'

/**
 * 
 * @returns {Promise<{
 *   success: boolean,
 *   data: Array<{
 *     id: string,
 *     name: string,
 *     description: string,
 *     icon_url: string,
 *     level: number,
 *     category: string,
 *     is_active: boolean,
 *     rules: {
 *       type: string,
 *       conditions: Array<{
 *         field: string,
 *         operator: string,
 *         value: any
 *       }>
 *     },
 *     nft_metadata: {
 *       name: string,
 *       description: string,
 *       image: string,
 *       attributes: Array<{
 *         trait_type: string,
 *         value: string
 *       }>
 *     }
 *   }>
 * }>}
 */
export function getAllAchievements() {
  return request({
    url: '/api/v1/achievements',
    method: 'GET'
  })
}

/**
 * 
 * @returns {Promise<{
 *   success: boolean,
 *   data: Array<{
 *     id: string,
 *     user_id: string,
 *     achievement_id: string,
 *     earned_at: string,
 *     event_data: {
 *       trigger_event: string,
 *       level_id?: string
 *     },
 *     achievement: {
 *       id: string,
 *       name: string,
 *       description: string,
 *       icon_url: string,
 *       level: number,
 *       is_active: boolean,
 *       nft_metadata: {
 *         name: string,
 *         description: string,
 *         image: string
 *       }
 *     }
 *   }>
 * }>}
 */
export function getUserAchievements() {
  return request({
    url: '/api/v1/users/achievements',
    method: 'GET'
  })
}

/**
 * 
 * @returns {Promise<{
 *   success: boolean,
 *   message: string
 * }>}
 */
export function evaluateAchievements() {
  return request({
    url: '/api/v1/achievements/evaluate',
    method: 'POST'
  })
}

